import React, {useState} from "react";
import ProductsInfo from "../../Data/ProductInfo";
import LoadProducts from "../GeneralModules/LoadProducts";
import ProductLayout from "../Products/ProductLayout/ProductLayout";

const Laliga = (props) => {
    const [state, setState] = useState({
        loaded_product_number: 6
    });
    const {loaded_product_number} = state;
    const laligaProduct = ProductsInfo.filter((product) => {
        return product.league === "La Liga";
    });
    let loadedProducts = [];
    laligaProduct.forEach((product) => {
        if (loadedProducts.length < loaded_product_number) {
            loadedProducts.push(product)
        }
    })
    return (
        <div className="leage la-liga">
            <div className="each-league-title">La Liga<br/>cuồng nhiệt xứ sở bò tót</div>
            <div className="leagues-products-layout">
                <div className="container-fluid">
                    <div className="row">
                    {loadedProducts.map((product) => {
                        return (
                            <ProductLayout product={product} key={product.id}/>
                        )
                    })}
                    </div>
                </div>
                <div className="control-loaded-quantity">
                    <button className="load-btn" onClick={(ev)=>{LoadProducts(ev, loaded_product_number, laligaProduct.length, setState)}}>
                        {loaded_product_number < laligaProduct.length ? "Xem thêm" : "Ẩn bớt"}
                    </button>
                </div>
            </div>
        </div>
    );
};

export default Laliga;